.ugo-icon-button-container {
    display: flex;
    flex-direction: row;
    align-items: center;

    .ugo-icon {
        height: 36px;
        width: 36px;
        border-radius: 20rem;
    }
    
    .primary-icon {
        background-color: rgba(71, 71, 71, 0.458);
        border: 1px solid rgba(71, 71, 71, 0.458)
    }
    
    .secondary-icon {
        background-color: rgba(185, 185, 185, 0.458);
        border: 1px solid rgba(185, 185, 185, 0.458);
    }
}

.hover-effect {
    cursor: pointer;
    fill: var(--secondary-color);
    transition: transform 0.5s ease;
    transform: scale(1); /* Default state */
}
  
.hover-effect:hover {
    transform: scale(1.5); /* Zoom in on hover */
}
